.background {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    color: #010101;
    background-color: #950024;
}

.container {
    font-family: 'Roboto';
    min-height: 100vh;
    width: 1100px;
    display: flex;
    flex-direction: column;
    font-size: 40;
    color: #010101;
    background-color: #FAFFBC;
    box-shadow: 0px 0px 35px 50px rgba(34, 60, 80, 0.4);
    padding-bottom: 40px;
}

.pad15{
    padding: 15px;
}

.centre{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-nav {
    display: flex;
    align-items: center;
    padding: 15px;
    box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.header-nav__item{
    text-decoration: none;
    padding: 5px 10px;
    margin-right: 10px;
    margin-bottom: 5px;
    font-size: 28px;
    color: #010101;
    border: 1px solid #FAFFBC;
    transition: all 0.3s ease;
}

.header-nav__item:hover,:focus {
    color: #950024;
    border-bottom: 1px solid #950024;
}

.active {
    color: #950024;
    border: 1px solid #950024;
    border-radius: 5px;
    font-weight: 700;
}

.logo{
    width: 70px;
    margin-left: auto;
}

.title-box {
    display: flex;
    justify-content: center;
    font-size: 32px;
    color: #950024;
    font-style: italic;
}

.movie-name {
    color: #950024;
    font-weight: 600;
    width: 300px;
}

.tranding-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 0;
}

.trending-list__item--link {
    text-decoration: none;
    position: relative;
}


.trending-list__item{
    margin-bottom: 15px;
    border: 1px solid #950024;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    text-align: center;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.trending-list__item img {
    width: 300px;
}

.trending-list__item p {
    margin: 10px 0;
}

.trending-list__item--link::after {
    content: "";
    width: 100%;
    height: 2px;

    background-color: #950024;
    border-radius: 2px;

    position: absolute;
    left: 0;
    bottom: 0;
    
    transform: scaleX(0);
    transform-origin: left;
    transition: transform .5s ease;
}

.trending-list__item--link:hover::after {
    transform: scaleX(1);
}

.btn-box{
    display: flex;
    align-items: center;
}

.btn{
    background-color: #FAFFBC;
    color: #950024;
    border: 2px solid #950024;
    padding: 10px 15px;
    border-radius: 5px;
    font-size: 24px;
    font-weight: 600;
    cursor: pointer;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    transform: scaleX(1);
    transition: transform .3s ease;
}

.btn:hover,:focus {
    transform: scale(1.2);
}

.btn-box {
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

.btn-mar-right {
    margin-right: 30px;
}

.one_movie_box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 900px;
    margin: 50px 0;
}

.poster_one_movie {
    box-shadow: 0px 5px 10px 10px rgba(34, 60, 80, 0.2);
}

.one_movie_info--item {
    list-style: none;
    margin-bottom: 10px;
}

.one_movie_info--item h2 {
    color: #950024;
    margin-bottom: 3px;
}

.one_movie_info--item b {
    margin-right: 10px;
}

.genres {
    display: flex;
}

.genres p {
    margin-top: 5px;
    margin-right: 5px;
    margin-bottom: 0;
}

.more_info {
    padding: 0 40px;
}


.more_info_titile {
    margin-bottom: 10px;
}

.more_info a {
    text-decoration: none;
    color: #950024;
    margin: 5px;
}

.cast_list h3 {
    margin-top: 40px;
    margin-left: 40px;
    margin-bottom: 0;
}

.cast {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 0 40px;
}

.cast-item {
    border: 1px solid #950024;
    border-radius: 5px;
    list-style: none;
    width: 100px;
    text-align: center;
    margin: 10px;
}

.cast_item__img{
    width: 100px;
    display: block;
}

.cast-item  p {
    margin: 5px 0;
}

.movies-title {
    color: #950024;
    font-size: 32px;
    margin-top: 15px;
    margin: 15px 0 15px 40px;
}

form input {
    margin: 0 10px 0 40px;
    outline: #950024;
}

form input:focus {
    transform: scale(1);
}

.input-btn {
    font-size: 16px;
    color: #950024;
    border: 2px solid #950024;
    border-radius: 5px;
    background-color: #FAFFBC;
}